import Vue from 'vue'
import Router from 'vue-router'
import Index from '../views/index.vue'
import appRouters from './appRouter.js'
import otherRouter from './otherRouter.js'

Vue.use(Router)

const routes = [
  {
    path: '/',
    name: 'index',
    component: Index,
    children:[
      {
        path: '/',
        name: 'home',
        component: resolve => {
          require(['@/views/home/home.vue'], resolve)
        }
      },
      ...otherRouter,
    ]
  },
  ...appRouters,
]
export default new Router({
  routes
})



