/*
*   公共routers
*   所有人都有权限
*
* */

export default [
  {
    path: '/login',
    name: 'login',
    component: resolve => {
      require(['@/views/login/login.vue'], resolve)
    }
  },
  {
    path: '/403',
    meta: {
      title: '403-权限不足'
    },
    name: 'error-403',
    component: resolve => {
      require(['@//views/error-page/403.vue'], resolve)
    }
  },
  {
    path: '/500',
    meta: {
      title: '500-服务端错误'
    },
    name: 'error-500',
    component: resolve => {
      require(['@/views/error-page/500.vue'], resolve)
    }
  },
  {
    path: '/*',
    name: 'error-404',
    meta: {
      title: '404-页面不存在'
    },
    component: resolve => {
      require(['@/views/error-page/404.vue'], resolve)
    }
  },
]



