<template>
  <el-row class="wrapper">
    <el-col :span="24" class="header">
        <h1 @click="$router.push('/')" class="index-title">实有人口</h1>
        <el-button type="danger" class="change"  @click="openNav = !openNav" icon="el-icon-menu"></el-button>
    </el-col>
    <el-col :span="24" class="index-page">
      <the-layout-sidebar :open-nav="openNav"></the-layout-sidebar>
      <div class="index-page-box" :class="{'page-content-hide-aside': !openNav}">
        <router-view/>
      </div>
    </el-col>
  </el-row>
</template>

<script>
  import TheLayoutSidebar from './layout/the-layout-sidebar.vue'
  export default {
    name: 'index_home',
    data () {
      return {
        openNav: true
      }
    },
    components:{
      TheLayoutSidebar
    }
  }
</script>

<style scoped lang="less">
  .wrapper{
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    .index-title{
      display: inline-block;
      padding: 0 .2rem;
      height: 100%;
      color: #fff;
      font-weight: 900;
      cursor: pointer;
      font-size: .4rem;
    }
    .header{
      height: .6rem;
      display: flex;
      align-items: center;
      background-color: #ccc;
    }
    .index-page{
      position: absolute;
      top: .6rem;
      bottom: 0;
      left: 0;
      background-color: rgba(33,33,33,.3);
    }
    .index-page-box{
      position: absolute;
      left: 2.4rem;
      right: 0;
      height: 100%;
      transition: left 600ms;
    }
    .page-content-hide-aside{
      left: 0;
    }
  }
</style>



