<template>
  <aside class="sidebar" :class="{'sidebar-hide': !openNav}">
    <vue-scroll>
      <el-menu
        default-active="2"
        background-color="#545c64"
        text-color="#fff"
        router
        active-text-color="#ffd04b">
        <el-menu-item :index="item.path" v-for="(item, index) in permissions" :key="index">
          <template slot="title">
            <i :class="item.icon"></i>
            <span>{{item.title}}</span>
          </template>
        </el-menu-item>
      </el-menu>
    </vue-scroll>
  </aside>
</template>

<script>
  import outherRouter from '../../router/otherRouter.js'
  export default {
    name: 'TheLayoutSidebar',
    props: ['openNav'],
    data () {
      return {
        permissions: [
          ...outherRouter
        ]
      }
    },
    methods: {

    },
    created(){

    }
  }
</script>

<style scoped lang="less">
  .sidebar {
    width: 2.4rem;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    border-right: 1px solid #e6e6e6;
    background-color: #ffffff;
    transition: left 600ms;
    z-index: 99;
    .sidebar-menu {
      border: none;
      height: 100%;
    }
  }

  .sidebar-hide {
    left: -2.4rem;
  }
</style>



